import React, { useState } from 'react';
import { CellType } from '../../types';

interface ColumnTypeSelectorProps {
  onSelectType: (type: CellType, options?: string[]) => void;
  onCancel: () => void;
}

const ColumnTypeSelector: React.FC<ColumnTypeSelectorProps> = ({
  onSelectType,
  onCancel
}) => {
  const [selectedType, setSelectedType] = useState<CellType>('text');
  const [selectOptions, setSelectOptions] = useState<string>('');

  const columnTypes = [
    { type: 'text' as CellType, label: '텍스트', icon: '📝', desc: '일반 텍스트 입력' },
    { type: 'number' as CellType, label: '숫자', icon: '🔢', desc: '숫자 입력 (계산 가능)' },
    { type: 'date' as CellType, label: '날짜', icon: '📅', desc: '날짜 선택기' },
    { type: 'checkbox' as CellType, label: '체크박스', icon: '☑️', desc: '체크/언체크' },
    { type: 'select' as CellType, label: '선택형', icon: '📋', desc: '드롭다운 선택' },
    { type: 'formula' as CellType, label: '수식', icon: '🧮', desc: '계산 수식' }
  ];

  const handleConfirm = () => {
    let options: string[] | undefined;
    
    if (selectedType === 'select' && selectOptions.trim()) {
      options = selectOptions.split(',').map(opt => opt.trim()).filter(opt => opt);
    }
    
    onSelectType(selectedType, options);
  };

  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0,0,0,0.5)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 10000
    }}>
      <div style={{
        backgroundColor: 'white',
        borderRadius: '12px',
        padding: '24px',
        minWidth: '400px',
        maxWidth: '500px',
        boxShadow: '0 10px 25px rgba(0,0,0,0.25)'
      }}>
        <h3 style={{
          margin: '0 0 16px 0',
          fontSize: '18px',
          fontWeight: 'bold',
          color: '#333'
        }}>
          열 타입 선택
        </h3>
        
        <div style={{
          marginBottom: '20px'
        }}>
          {columnTypes.map(({ type, label, icon, desc }) => (
            <div
              key={type}
              style={{
                display: 'flex',
                alignItems: 'center',
                padding: '12px',
                marginBottom: '8px',
                border: `2px solid ${selectedType === type ? '#007bff' : '#e0e0e0'}`,
                borderRadius: '8px',
                cursor: 'pointer',
                backgroundColor: selectedType === type ? '#f0f8ff' : 'white',
                transition: 'all 0.2s ease'
              }}
              onClick={() => setSelectedType(type)}
            >
              <div style={{
                fontSize: '24px',
                marginRight: '12px'
              }}>
                {icon}
              </div>
              <div style={{ flex: 1 }}>
                <div style={{
                  fontWeight: 'bold',
                  fontSize: '14px',
                  marginBottom: '2px'
                }}>
                  {label}
                </div>
                <div style={{
                  fontSize: '12px',
                  color: '#666'
                }}>
                  {desc}
                </div>
              </div>
              <div style={{
                width: '20px',
                height: '20px',
                borderRadius: '50%',
                border: `2px solid ${selectedType === type ? '#007bff' : '#ccc'}`,
                backgroundColor: selectedType === type ? '#007bff' : 'white',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }}>
                {selectedType === type && (
                  <div style={{
                    width: '10px',
                    height: '10px',
                    borderRadius: '50%',
                    backgroundColor: 'white'
                  }} />
                )}
              </div>
            </div>
          ))}
        </div>

        {/* 선택형 타입일 때 옵션 입력 */}
        {selectedType === 'select' && (
          <div style={{ marginBottom: '20px' }}>
            <label style={{
              display: 'block',
              marginBottom: '8px',
              fontSize: '14px',
              fontWeight: 'bold',
              color: '#333'
            }}>
              선택 옵션 (콤마로 구분)
            </label>
            <input
              type="text"
              value={selectOptions}
              onChange={(e) => setSelectOptions(e.target.value)}
              placeholder="옵션1, 옵션2, 옵션3"
              style={{
                width: '100%',
                padding: '8px 12px',
                border: '1px solid #ccc',
                borderRadius: '6px',
                fontSize: '14px'
              }}
              autoFocus
            />
            <div style={{
              fontSize: '12px',
              color: '#666',
              marginTop: '4px'
            }}>
              예: 진행중, 완료, 대기, 취소
            </div>
          </div>
        )}

        <div style={{
          display: 'flex',
          gap: '12px',
          justifyContent: 'flex-end'
        }}>
          <button
            onClick={onCancel}
            style={{
              padding: '8px 16px',
              border: '1px solid #ccc',
              borderRadius: '6px',
              backgroundColor: 'white',
              color: '#666',
              cursor: 'pointer',
              fontSize: '14px'
            }}
          >
            취소
          </button>
          <button
            onClick={handleConfirm}
            style={{
              padding: '8px 16px',
              border: 'none',
              borderRadius: '6px',
              backgroundColor: '#007bff',
              color: 'white',
              cursor: 'pointer',
              fontSize: '14px',
              fontWeight: 'bold'
            }}
          >
            확인
          </button>
        </div>
      </div>
    </div>
  );
};

export default ColumnTypeSelector;